export default "\n/* eslint-disable */\nimport React, {\n  useState,\n} from 'react';\nimport ApplicationBase from 'terra-application/lib/application-base';\nimport IconSearch from 'terra-icon/lib/icon/IconSearch';\nimport IconPill from 'terra-icon/lib/icon/IconPill';\nimport IconVisualization from 'terra-icon/lib/icon/IconVisualization';\nimport IconLightbulb from 'terra-icon/lib/icon/IconLightbulb';\nimport Placeholder from 'terra-doc-template/lib/Placeholder';\nimport ApplicationNavigation from '../../../ApplicationNavigation';\n\n\nconst titleConfig = {\n  title: 'Test Title',\n};\n\nconst userConfig = {\n  name: 'Test Name',\n  initials: 'TN',\n};\n\nconst extensionItems = [\n  {\n    icon: <IconSearch />,\n    key: 'item-a',\n    text: 'Item A',\n    metaData: {\n      test: 'a',\n    },\n  }, {\n    icon: <IconPill />,\n    key: 'item-b',\n    text: 'Item B',\n    metaData: {\n      test: 'b',\n    },\n  }, {\n    icon: <IconVisualization />,\n    key: 'item-c',\n    text: 'Item C',\n    metaData: {\n      test: 'c',\n    },\n  }, {\n    icon: <IconLightbulb />,\n    key: 'item-d',\n    text: 'Item D',\n    metaData: {\n      test: 'd',\n    },\n  },\n];\n\nconst navigationItems = [\n  {\n    key: '/page_1',\n    text: 'Page 1',\n    metaData: {\n      display: 'Page 1',\n    },\n  }, {\n    key: '/page_2',\n    text: 'Page 2',\n    metaData: {\n      display: 'Page 2',\n    },\n  }, {\n    key: '/page_3',\n    text: 'Page 3',\n    metaData: {\n      display: 'Page 3',\n    },\n  }, {\n    key: '/page_4',\n    text: 'Page 4',\n    metaData: {\n      display: 'Page 4',\n    },\n  }, {\n    key: '/page_5',\n    text: 'Page 5',\n    metaData: {\n      display: 'Page 5',\n    },\n  }, {\n    key: '/page_6',\n    text: 'Page 6',\n    metaData: {\n      display: 'Page 6',\n    },\n  }, {\n    key: '/page_7',\n    text: 'Page 7',\n    metaData: {\n      display: 'Page 7',\n    },\n  }, {\n    key: '/page_8',\n    text: 'Page 8',\n    metaData: {\n      display: 'Page 8',\n    },\n  },\n];\n\nconst utilityItems = [\n  {\n    icon: <IconSearch />,\n    key: 'item-a',\n    text: 'Item A',\n    metaData: {\n      test: 'a',\n    },\n  }, {\n    icon: <IconPill />,\n    key: 'item-b',\n    text: 'Item B',\n    metaData: {\n      test: 'b',\n    },\n  }, {\n    icon: <IconVisualization />,\n    key: 'item-c',\n    text: 'Item C',\n    metaData: {\n      test: 'c',\n    },\n  }, {\n    icon: <IconLightbulb />,\n    key: 'item-d',\n    text: 'Item D',\n    metaData: {\n      test: 'd',\n    },\n  },\n];\n\nconst ExampleApplication = ({ onAction }) => {\n  const [activeKey, setActiveKey] = useState(navigationItems[0].key);\n\n  return (\n    <ApplicationBase locale=\"en-US\">\n      <ApplicationNavigation\n        titleConfig={titleConfig}\n        userConfig={userConfig}\n        extensionItems={extensionItems}\n        onSelectExtensionItem={onAction}\n        navigationItems={navigationItems}\n        activeNavigationItemKey={activeKey}\n        onSelectNavigationItem={key => setActiveKey(key)}\n        utilityItems={utilityItems}\n        onSelectUtilityItem={onAction}\n        onSelectSettings={() => onAction('settings')} // eslint-disable-line no-alert\n        onSelectHelp={() => onAction('help')} // eslint-disable-line no-alert\n        onSelectLogout={() => onAction('logout')} // eslint-disable-line no-alert\n      >\n        <Placeholder title={activeKey} />\n      </ApplicationNavigation>\n    </ApplicationBase>\n  );\n};\n\nexport default ExampleApplication;\n"